	import {  
        mapMutations  
    } from 'vuex';
	import uniGrid from "@/components/uni-grid/uni-grid.vue"
	import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
	import uniRequest from 'uni-request';
	export default{
		data(){
			return {
				username: '',
				password: '',
				logining: false,
				remember:true,
				next:'',
			}
		},
		components: {uniGrid,uniGridItem},
		onLoad(){
			this.message()
		},
		methods: {
			...mapMutations(['login']),
			message(){
				this.$api.msg('请先登录')
			},
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();  
			},
			toRegist(){
				this.$api.msg('去注册');
			},
			changePayType(){
				this.remember = !this.remember
				console.log(this.remember)
			},
			
			async toLogin(){
				this.logining = true;
				const {username, password} = this;
				// 用户名验证
				if(!username){ 
					this.$api.msg('请输入用户名');
					this.logining = false;
					return
				}else if(username.length < 3){
					this.$api.msg('用户名不能少于3位');
					this.logining = false;
					return
				}
				// 密码验证
				if(!password){
					this.$api.msg('请输入密码');
					this.logining = false;
					return
				}else if(password.length < 8){
					this.$api.msg('密码不能少于8位');
					this.logining = false;
					return
				}
				console.log(username, password)
				const sendData = {
					username,
					password
				};
				const response = await uniRequest.post('/auth2/',sendData);
				console.log(response)
				if(response.status === 200){
					if(this.remember === true){
						this.login(response.data);
						uni.navigateBack();  
					}
				}else{
					this.$api.msg('用户名/手机号或密码输入错误');
					this.logining = false;
				}
			},
			
			// qq登录
			async qq_login(){
				await uniRequest({
					url:'/oauth/qq/authorization/?next=http://192.168.1.20:8000/h5/pages/public/oauthCallback',
					method:'get',
				}).then(res=>{
					console.log(res)
					if(res.status === 200){
						location.href = res.data.login_url;
						console.log(res.data.login_url)
					}
				}).catch(error=>{
				   console.log(error)
				   this.$toast({
				     message:'服务器错误'
				   })
				})
			},
			
			// 央采
			yc_login(path){
				location.href = path
			},
			
			// qq登录
			  async QQ_login(){
				var vm = this;
				uni.getProvider({
					service: 'oauth',
					success: function (res) {
						console.log(res.provider) //qq,xiaomi,sinaweibo,weixin
						vm.log = res.provider
						if (~res.provider.indexOf('qq')) {
							uni.login({
								provider: 'qq', 
								success:async function (loginRes) {
									var access_token=loginRes.authResult.access_token;
									uni.getUserInfo({
										provider: 'qq',
										success: function (infoRes) {
											console.log(infoRes)
											/* var formdata={
												nickName:infoRes.userInfo.nickname,
												gender:infoRes.userInfo.gender=='男'?0:1,
												openId:infoRes.userInfo.openId,
												access_token:access_token,
											}; */
											uniRequest.get('/oauth/qq/user/openid?openid='+infoRes.userInfo.openId).then(res=>{
												console.log(res)
												if(res.data.message === false){
													uni.navigateTo({
														url: "/pages/public/oauthCallback?openid="+infoRes.userInfo.openId
													})
												}else if(res.data.message === true){
													vm.login(res.data)
													uni.navigateBack();
												}
											}).catch(error=>{
											   console.log(error)
											})
										}
									})
								}
							});
						}
					}
				});  
			  },
			  
			  
			  
			  wx_login(){
				  uni.login({
				  	provider: "weixin",
				  	success: (res) => {
				  		uni.getUserInfo({
				  		    provider: 'weixin',
				  		    success: function (infoRes) {
								console.log(infoRes)
				  				let formdata={
				  					nickName:infoRes.userInfo.nickName,
				  					gender:infoRes.userInfo.gender,
				  					openId:infoRes.userInfo.openId,
				  					unionId:infoRes.userInfo.unionId
				  				};
								/* uniRequest.get('/oauth/wx/user/openid?openid='+infoRes.userInfo.openId).then(res=>{
									console.log(res)
									if(res.data.message === false){
										uni.navigateTo({
											url: "/pages/public/oauthCallback?openid="+infoRes.userInfo.openId
										})
									}else if(res.data.message === true){
										console.log('111')
										vm.login(res.data)
										uni.navigateBack();
									}
								}).catch(error=>{
								   console.log(error)
								}) */
				  			}
				  		})
				  	},
				})
			  }
		},

	}
